.task-item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 20px;
    border: 1px solid rgb(181, 181, 189);
    padding: 10px;
    border-radius: 5px;
  }
  
  .task-item input[type="checkbox"] {
    margin-bottom: 10px;
  }
  
  .task-item button {
    width: 100%;
    padding: 5px 10px;
    margin-top: 5px;
    border: 1px solid blue;
    border-radius: 5px;
    background-color: white;
    cursor: pointer;
  }
  
  .task-item button:hover {
    background-color: #bccfab;
  }
  
  .task-item h3 {
    margin-bottom: 5px;
  }
  
  .task-item p {
    margin: 0;
  }
  
  @media only screen and (max-width: 768px) {
    .task-item {
      padding: 5px;
    }
  }
  